import React, {ReactNode} from 'react';
import clsx from 'clsx';
import {Button} from '../../ui/buttons/button';
import {ProgressCircle} from '../../ui/progress/progress-circle';
import {appearanceState} from './appearance-store';
import {Trans} from '../../i18n/trans';
import {useActiveUpload} from '../../uploads/uploader/use-active-upload';
import {UploadInputType} from '../../uploads/types/upload-input-config';
import {useController} from 'react-hook-form';
import {mergeProps} from '@react-aria/utils';
import {ProgressBar} from '../../ui/progress/progress-bar';
import {Disk} from '../../uploads/types/backend-metadata';

const TwoMB = 2 * 1024 * 1024;

interface ImageSelectorProps {
  className?: string;
  label: ReactNode;
  description?: ReactNode;
  value?: string;
  onChange?: (newValue: string) => void;
  highlightSelector?: string;
  defaultValue?: string;
  diskPrefix: string;
}
export function ImageSelector({
  className,
  label,
  description,
  value,
  onChange,
  highlightSelector,
  defaultValue,
  diskPrefix,
}: ImageSelectorProps) {
  const {selectAndUploadFile, entry, percentage, uploadStatus} =
    useActiveUpload();

  const imageUrl = value || entry?.url;

  const handleUpload = () => {
    selectAndUploadFile({
      restrictions: {
        allowedFileTypes: [UploadInputType.image],
        maxFileSize: TwoMB,
      },
      metadata: {
        diskPrefix,
        disk: Disk.public,
      },
      onSuccess: entry => {
        onChange?.(entry.url);
      },
    });
  };

  const uploadButton = (
    <Button
      disabled={uploadStatus === 'inProgress'}
      startIcon={
        uploadStatus === 'inProgress' && (
          <ProgressCircle
            size="sm"
            value={percentage}
            aria-label="upload progress"
          />
        )
      }
      variant="outline"
      color="primary"
      className="w-full"
      onClick={() => {
        handleUpload();
      }}
    >
      <Trans message="Select image" />
    </Button>
  );

  return (
    <div
      className={clsx('text-sm', className)}
      onClick={() => {
        appearanceState().preview.setHighlight(highlightSelector);
      }}
    >
      <div>{label}</div>
      {description && <div className="text-muted mt-4">{description}</div>}
      <div className="mt-10">
        {!imageUrl ? (
          uploadButton
        ) : (
          <div>
            <div className="h-80 border rounded bg-chip p-6 relative overflow-hidden">
              <img
                className="h-full mx-auto rounded object-contain"
                src={imageUrl}
                alt=""
              />
              {uploadStatus === 'inProgress' && (
                <ProgressBar
                  value={percentage}
                  size="xs"
                  className="absolute left-0 bottom-0 w-full"
                  aria-label="upload progress"
                />
              )}
            </div>
            <div className="mt-10">
              <Button
                onClick={() => {
                  handleUpload();
                }}
                disabled={uploadStatus === 'inProgress'}
                className="mr-10"
                variant="outline"
                color="primary"
                size="xs"
              >
                <Trans message="Replace" />
              </Button>
              {defaultValue && (
                <Button
                  variant="outline"
                  color="primary"
                  size="xs"
                  onClick={() => {
                    onChange?.(defaultValue);
                  }}
                >
                  <Trans message="Use default" />
                </Button>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

interface FormImageSelectorProps extends ImageSelectorProps {
  name: string;
}
export function FormImageSelector(props: FormImageSelectorProps) {
  const {
    field: {onChange, value = null},
  } = useController({
    name: props.name,
  });

  const formProps: Partial<ImageSelectorProps> = {
    onChange,
    value,
  };

  return <ImageSelector {...mergeProps(formProps, props)} />;
}
